import { Flex, Text } from "@chakra-ui/react";
import MotifBySize from "./MotifBySize";
import { MotifBySizeItem } from "../utils/types";

interface Props {
  intro: string;
  motifBySize: MotifBySizeItem[];
}
export default function StitchPattern({ intro, motifBySize }: Props) {
  return (
    <Flex direction="column" gap={6}>
      <Text>{intro}</Text>
      <MotifBySize
        motifBySize={motifBySize.map((m) => {
          const { motif, sizes } = m;
          return { size: sizes.join(", "), motif };
        })}
      />
    </Flex>
  );
}
